	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 14 – Les présentations","texte":{"childMarkdownRemark":{"html":"<p>On entre tout dans la maison. J’entre en dernier, Clara était entrée en premier et guidait les personnes au salon où tout le monde va pouvoir s’installer. Les gens vont s’asseoir, Tiana à côté de Sam qui préfère rester debout à cause de ses ailes. Tout le monde s’assoit en silence.</p>\n<p>Je vais au centre de la pièce.</p>\n<p>Wamien : Donc, salut tout le monde. Mon nom est Wamien et je suis heureux de vous voir tout en parfaite santé. Question qu’on se connaisse plus, j’aimerais que l’on fasse les présentations. Comme je disais, mon nom est Wamien et ce que j’ai de spécial, c’est que j’ai une connexion avec tous les appareils électroniques et je suis un cyborg.</p>\n<p>Certains font des « oh » de surprise, ce qui me fait sourire doucement. Je porte mon regard sur Clara lui faisant un petit signe de se présenter aussi.</p>\n<p>Clara : Bonjour, bonjour. Mon nom est Clara et je suis capable de me régénérer si je me blesse, ainsi que de guérir les gens s’ils sont blessés.<br>\nTiana prend la parole : Coucou moi c’est Tiana et je suis flexible, mon corps n’a donc pas les limitations que les humains ont.</p>\n<p>Elle fait une démonstration en faisant tourner son bras dans plusieurs directions ce qui est impossible à faire pour les humains.</p>\n<p>Sam : Yo moi c’est Sam, comme vous pouvez le voir, j’ai des ailes d’ange dans le dos, ce qui signifie que je sais voler.</p>\n<p>Un jeune homme avec des lunettes prend la parole : Salut, moi c’est Kameron, je suis capable de générer du feu à partir de n’importe quelle partie de mon corps.</p>\n<p>Une femme prend la parole : Bonjour à tout, mon nom est Thalie et je sais transformer mon corps en glu indestructible.<br>\nUn jeune homme d’environ 17 ans prend la parole : Salut salut, moi c’est Alexis et je sais faire de la télékinésie.<br>\nLe garçon à côté de lui prend la parole : Moi c’est Bailey et je suis capable de contrôler et de créer de l’électricité.<br>\nTim : Moi, c’est Tim et je suis super costaud, en plus d’avoir une peau qui ne peut pas se blesser.<br>\nUne fille qui était restée silencieuse tout le long du voyage prend la parole : Moi c’est Kémyanne, mais vous pouvez m’appeler Kémy, je sais passer à travers les murs.<br>\nUne jeune femme un peu plus souriante prend la parole : Moi c’est Louann, mais vous pouvez aussi m’appeler Lou ou la Fée de l’eau parce que je sais manipuler l’eau et la faire changer d’état.<br>\nUne fille était restée sur le bord du mur, un peu en retrait. Elle prend la parole en dernier : Pff vos pouvoirs sont rien comparés à moi, je suis Saeko la vampire! Je suis super rapide, je sais voler par la volonté et je suis très forte.<br>\nWamien : Bien tout le monde et on va rester ici un moment et voir comment exploiter nos pouvoirs à leur maximum. Mis à part Clara on a tout eu des pouvoirs par la voie de la science. Malgré tout, si on a réussi à rester en vie, c’est simplement parce que nos gênes étaient prévues pour fonctionner.<br>\nTiana : Alors, c’est comme si le fait qu’on ait été des sujets de tests avait devancé l’apparition de nos pouvoirs?<br>\nWamien : Oui, nos enfants auraient eu plus de chances d’avoir des pouvoirs.<br>\nSam : Et Clara est cet exemple.<br>\nClara rougit : Ouais si on peut dire ça comme ça.<br>\nSaeko : Pff, les enfants, ça sert juste à pleurer et remplir des couches.<br>\nClara : Hé oh, un peu de respect s’il te plaît.<br>\nSaeko : Pourquoi? Je suis le genre de fille à faire ses trucs en solo.<br>\nWamien : Bah tu vas devoir t’habituer à rester en groupe parce qu’on doit rester ensemble et discret au maximum.<br>\nSam : Genre, je vais devoir éviter de sortir?<br>\nWamien : Du moins d’aller dans les endroits avec trop de personnes.<br>\nSam : Hum, je vois.<br>\nTim : Et moi alors?<br>\nWamien : Toi ça devrait aller, tu as presque l’air d’un monsieur muscle avec ta carrure.</p>\n<p>Quelques rires se font entendre, ce qui me fait sourire.</p>\n<p>Tim ne répond pas à la blague et se lève brusquement : J’ai faim!<br>\nJe me tourne lentement vers Clara : On va chercher de la pizza?<br>\nClara : D’accord, je pars avec Alexis et Kameron. Vous venez les gars.<br>\nAlexis : Bien sûr! De la pizza, ça ne se refuse pas!</p>\n<p>Avant de partir, ils font une liste des sortes de pizza que tout le monde souhaite et partent tous les trois. Tiana et Sam vont dans la cuisine placer la table pour nous tous. Je vais à l’étage et fais un plan rapide des chambres.</p>\n<p>Je reste parler avec les autres et rassurer ceux qui restaient un peu inquiets de savoir ce qu’on va tout devenir. Une bonne demi-heure plus tard. Clara, Alexis et Kameron reviennent avec les boîtes de pizza. Tout le monde vient s’installer à table et on mange avec appétit. Saeko reste devant son assiette et sa pointe de pizza pepperoni fromage.</p>\n<p>Je la regarde passif : Tu ne manges pas?<br>\nSaeko : Appétit de moineau et puis j’aime mieux le sang.</p>\n<p>Certains la regardent semblant penser qu’elle n’avait pas sa place parmi nous ce qui est entièrement faux.</p>\n<p>Wamien : Personne ne te force à manger, mais évite d’essayer de boire notre sang tu veux?<br>\nSaeko : Ça devrait aller, personne n’a un sang assez bon pour moi ici.</p>\n<p>Elle se lève et monte à l’étage.</p>"}},"chapitreAvant":"chapitre-13-la-rebellion-partie-2","chapitreApres":"chapitre-15-black-eagles","nomRoman":"../le-premier-cyborg-tome-1","slug":"chapitre-14-les-presentations"}},"pathContext":{"slug":"chapitre-14-les-presentations"}}

